* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.search-form {
  font: inherit;
  min-width: 320px;
  border-color: #00f;
  border-radius: 8px;
  justify-content: center;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
  padding: 8px;
  display: flex;
}

::placeholder {
  color: #00f;
  font-size: 18px;
}

.button {
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.button.is-hidden, .button > .spinner.is-hidden {
  display: none;
}

.gallery {
  max-width: calc(100vw - 80px);
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  margin: 0 auto 30px;
  padding: 0;
  list-style: none;
  display: grid;
}

h1, h2, p {
  margin-top: 0;
  margin-bottom: 0;
}

li {
  list-style: none;
}

.country-item {
  min-width: 100px;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.country-name {
  margin-left: 20px;
}

.country-list {
  padding-left: 0;
}

.country-info {
  flex-direction: column;
  display: flex;
}

.search-country-name {
  font-size: 36px;
}

.card-text {
  font-size: 18px;
}

.search-country-flag {
  margin-top: 20px;
  margin-bottom: 20px;
}

